  endDateCounter(daysAmount, day, month, year) {
    let endDay = day + Number(daysAmount)
    let endMonth = month
    switch (month) {
      case 1: case 3: case 5: case 7: case 8: case 10: case 12:
        if (endDay > 31) {
          endDay -= 31
          endMonth += 1
        }
        break;
      case 4: case 6: case 9: case 11:
        if (endDay > 30) {
          endDay -= 30
          endMonth += 1
        }
        break;
      case 2:
        if (endDay > 28) {
          endDay -= 28
          endMonth += 1
        }
        break;
    }
    let endDate = endDay + '.' + endMonth + '.' + year
    return endDate
  }

